import React from 'react';
import Link from 'gatsby-link';
import {
  Grid,
  Row,
  Col,
  Button,
  ButtonGroup,
  PageHeader,
  ButtonToolbar,
  Navbar,
  Nav,
  NavItem,
  MenuItem,
  NavDropdown,
  Glyphicon,
  Breadcrumb,
  Badge,
  Jumbotron as Hero
} from 'react-bootstrap';

import Logo from '../components/logo/Logo';
import Navbars from '../components/sections/Navbars';
import Buttons from '../components/sections/Buttons';
import ButtonGroups from '../components/sections/ButtonGroups';
import Breadcrumbs from '../components/sections/Breadcrumbs';
import Pagination from '../components/sections/Pagination';
import Pager from '../components/sections/Pager';
import Pills from '../components/sections/Pills';
import Badges from '../components/sections/Badges';
import Jumbotron from '../components/sections/Jumbotron';
import Panels from '../components/sections/Panels';
import Tabs from '../components/sections/Tabs';
import Inputs from '../components/sections/Inputs';
import Typography from '../components/sections/Typography';
import Labels from '../components/sections/Labels';

function DemoRow({ children }) {
  return (
    <Row>
      <Col md={12}>{children}</Col>
    </Row>
  );
}

const IndexPage = () => (
  <div>
    <Hero className="hero">
      <Grid className="text-center">
        <div>
          <Logo width={120} height={130} />
        </div>
        <h1>Introducing UI Kit</h1>
        <br />
        <p>A new theme for bootstrap.</p>
        <br/>
        <div className="h4">
          <code>npm install --save @ui-kit/css</code>
        </div>
        <br /><br/>
        <Button bsStyle="info" className="btn-outline">
          View on Github
        </Button>
      </Grid>
    </Hero>
    <Grid fluid>
      <Row>
        <Col className="panel-body text-center" md={4}>
          <div className="h1"><Glyphicon className="text-info" glyph="tint" /></div><br/>
          <div><h4>Bootstrap theme</h4></div>
        </Col>
        <Col className="panel-body text-center" md={4}>
          <div className="h1"><Glyphicon className="text-info" glyph="ice-lolly-tasted" /></div><br/>
          <div><h4>Use with react-bootstrap</h4></div>
        </Col>
        <Col className="panel-body text-center" md={4}>
          <div className="h1"><Glyphicon className="text-info" glyph="wrench" /></div><br/>
          <div><h4>Custom components</h4></div>
        </Col>
      </Row>
      <hr />
      <Col md={3}> </Col>
      <Col md={9}>
        <Typography />
        <Navbars />

        <Row>
          <Col md={6}>
            <Buttons />
            <ButtonGroups />
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Inputs />
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Breadcrumbs />
            <Pagination />
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Pager />
            <Pills />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Badges />
            <Labels />
            <Panels />
            <Tabs />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Jumbotron />
          </Col>
        </Row>
      </Col>
    </Grid>
  </div>
);

export default IndexPage;



